import Vue from 'vue';
import Vuex from 'vuex';
import user from './user';
import evaluate from './evaluate';
import data from './data';

Vue.use(Vuex);


export default new Vuex.Store({
    modules: {
        user,
        evaluate,
        data
    }
})


